<template>    
    <Header :homeDisplay="homeDisplay"/>
    <div class="checktickets">
        <h3>[ 데이타베이스 백업하고 지우기 ]</h3>
        <form @submit.prevent="selectDateSubmit" class="mainlist">
            <v-date-picker v-model.range.number="date_range" :masks="masks" :max-date='maxDate'>
                <template v-slot="{ inputValue, inputEvents }">
                    <div class="flex justify-center items-center">
                        <div class="input-group mb-3">
                            <input type="text" class="form-control" :value="inputValue.start" v-on="inputEvents.start" aria-describedby="button-setting1">
                            <input type="text" class="form-control" :value="inputValue.end" v-on="inputEvents.end" aria-describedby="button-setting1">
                            <button type="submit" class="btn btn-primary" id="button-setting">기간설정</button>
                        </div>
                    </div>                
                </template>
            </v-date-picker>
        </form>
        <b-container v-if="this.download_index">
                    <span style="margin-left: 40%;">
                        <download-excel class="btn btn-primary" :data="this.allTickets" :fields="tickets_fields" 
                                worksheet="식수" :name="this.excelfname" size="lg" >내려받기(엑셀)</download-excel>
                    </span>                    
        </b-container>        
    </div>
</template>

<script>
import {ref} from 'vue';
import JsonExcel from "vue-json-excel3";
import Header from "@/components/Header.vue";
const homeDisplay = ref(true);

const now = new Date();
const date = new Date();
const mdate = new Date(date.setMonth(date.getMonth()-1));
const date_range = ref({
    start: mdate,
    end: now,
});
const maxDate = now;
const s_display=ref(false);
const excelfname=ref('');
const allTickets=([]);
const download_index = ref(false);

export default{
    name: "UserInfo",
    components: {
        Header, 
        downloadExcel:JsonExcel
    },
    data() {
        return{            
            date_range: date_range,
            masks: {
                input: 'YYYY-MM-DD',
            },
            maxDate:maxDate,
            s_display:s_display,
            excelfname:excelfname,
            tickets_fields: {
                "성명":"username",                
                "식수일자": "id",
                "uid": "uid",
                "전화번호":"telnum",
                "확정일자": "created_at",
            },
            homeDisplay: homeDisplay,
            allTickets:allTickets,
            download_index:download_index,
        }        
    },
    
    computed:{
        getAllTicketList() {        
            return this.$store.state.tickets.allticketList;            
        },        
    },    
    
    mounted() {
        this.$store.dispatch("fetchAlluserList");
        this.download_index= false;
    },

    watch:{
        getAllTicketList(val){
            
            if ( val.length > 0) {
            
                let users = this.$store.state.auth.userList;
                this.usersLength =users.length;
                if (users.length<0) {
                    this.$store.dispatch("fetchAlluserList");
                    users = this.$store.state.auth.userList;
                }
                if (users.length>0) {
                    
                    val.forEach( (ticket) =>{
                        var hasname = e => e.uid === ticket.uid;
                        let uname= users.find(hasname);
                        if( uname != null){
                            let ttdate = new Date(ticket.created_at).toLocaleString();                
                            this.allTickets.push({
                                id:ticket.id,
                                uid:ticket.uid,
                                username:uname.username,
                                telnum:uname.telnum,
                                created_at:ttdate,
                            });
                        }                        
                    });
                    
                }
            }            
        },
        getAlluserList(val) {
            this.userList = val;         
        }        
    },
    
    methods: {
        
        selectDateSubmit() {
            
            // const today = new Date();
            // let s_now = this.getYMD(today);
            let s_start = this.getYMD(new Date(date_range.value.start));
            let s_end = this.getYMD(new Date(date_range.value.end));
            this.excelfname = 'jeju_cafeteria'+'_'+s_start+'_'+s_end+'_'+'backup.xls';
            let start = new Date(s_start);
            let end = new Date(s_end);
            if( end.getTime() < start.getTime()) {
                let t = start;
                start = end;
                end = t;
            }            
            this.$store.dispatch("fetchAllTicketList",this.getYMD(start));
            this.$store.dispatch("deleteDB", this.getYMD(start)); 
            while (end>start) {
                    start.setDate(start.getDate()+1);
                    let s_ymd = this.getYMD(start);
                    this.$store.dispatch("fetchAllTicketList",s_ymd);
                    this.$store.dispatch("deleteDB", s_ymd);                    
            }
            this.download_index = true;
        },

        getYMD(date){

            const year = date.getFullYear();
            const month = date.getMonth()+1;
            const day = date.getDate();
            var todayString = year + "-";
            if (month < 10) {
                todayString += "0";
            }
            todayString += month + "-";
            if (day < 10) {
                todayString += "0";
            }
            todayString += day;

            return todayString;
        }
    },
}

</script>

<style scoped>
.topcontainer {
    padding: 20px;
}
.user_exit {
    padding: 40px;
    text-align: center;
}
.passregion{
    padding: 40px;
}

.checktickets{
    padding: 20px;
    text-align: center;
}
h3 {
    text-align: center;
}
.eat{
    color:blue;
}
.mainlist{
    padding: 10px;
}
</style>
